import { ICoord } from "./constants";

type CanvasImageSource =
  | HTMLImageElement
  | SVGImageElement
  | HTMLVideoElement
  | HTMLCanvasElement
  | ImageBitmap
  | OffscreenCanvas;

interface IGameObjectConfig {
  width: number;
  height: number;
  background?: string | CanvasImageSource;
  position?: ICoord;
}

interface EventListener {
  type: string;
  listener: CallableFunction;
}

type EventTypes = "update" | "draw";

class GameObject {
  public width: number;

  public height: number;

  public position: ICoord;

  public background: string | CanvasImageSource;

  private eventListeners: EventListener[] = [];

  constructor(config: IGameObjectConfig) {
    this.width = config.width;
    this.height = config.height;
    this.position = config.position;
    this.background = config.background || "black";
    this.position = config.position || { x: 0, y: 0 };
  }

  public triggerListener(type: EventTypes) {
    // get listeners
    const events = this.eventListeners.filter((event) => event.type === type);
    // run listeners
    events.forEach((event) => event.listener(this));
  }

  public addEventListener(type: EventTypes, listener: CallableFunction) {
    this.eventListeners.push({ type, listener });
  }
}

export default GameObject;
